import React, {Component} from 'react';  
import './App.css';
import Button from 'react-bootstrap/lib/Button'
import data1 from './stampStores1.json'

export default class Popup extends Component {
    
constructor(props) {
super(props);
this.state = {
    scroll_visible:false
}}

  //Function which is called when the component loads for the first time
  componentDidMount() {

    var scrollComponent = this;
    document.addEventListener("scroll", function(e){
      scrollComponent.toggleVisibility();
    })
  }

  //Scroll Visibility
  toggleVisibility(){
    if(window.pageYOffset>300){
      this.setState({
        scroll_visible:true
      });
    } else{
      this.setState({
        scroll_visible:false
      });
    }
  }

  // Scroll to top
  scrollToTop(){
    window.scrollTo({
      top:0,
      behavior:"smooth"
    })
  }

render() {  
    return (  
    <div className='popup'>  
    <div className='popup_inner'>  
    <h1 style={{fontSize:'100%'}}>{this.props.text}</h1>

    <div className='addmargin' className='stamp-column'>

        {data1.map((data)=>(

    <button className="stamp_button" onClick={()=>{this.props.handler(data); this.scrollToTop(); this.props.closeInfo()}}
    style={{fontSize:'80%'}}>
        <img src={require(`./icons/${data.icon}.png`)} width="20%"/>
        <br line-height="100%"></br>
        {data.name}
        </button>
    ))}

    <br line-height="100%"></br>

    </div>
   <Button variant="secondary" align="right" onClick={this.props.closePopup}>Close pop-up</Button>
    </div>  
    </div>
  
    );  
}  
}  




