import React, {Component} from 'react';
import { Tab, Tabs} from 'react-bootstrap';
import CustomerDetails from './CustomerDetails'
import Popup from './StampPopup'
import stores from './stores.json'
import axios from 'axios'
import logo from './Icon.png';
import stamp from './stamp.png';
import './App.css';
import { Map, Marker, GoogleApiWrapper, InfoWindow, Polygon } from 'google-maps-react';

// source: https://dev.to/skptricks/create-simple-popup-example-in-react-application-5g7f

const style = {
  width: '100%',
  height: '100%',
};

// draw up a boundary (didn't see the boundary being drawn up)
const littleindiaBoundary=[
  {lat: 1.306995, lng: 103.849370},
  {lat: 1.313306, lng: 103.854177},
  {lat: 1.309308, lng: 103.857776},
  {lat: 1.303510, lng: 103.854064},
  {lat: 1.306995, lng: 103.849370}
];

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      selectedCustomer: 1,
      selectedPlace:{},
      selectedItem:{name:"Welcome to Little India!"},
      initialLocation:{
        lat: 1.308480,
        lng: 103.853060
      },
      activeTab: props.activeTab || 1,
      showingInfoWindow:false,
      activeMarker:{},
      loading: true,
      category:"",
      showPopup:false
    }
    this.handleSelect = this.handleSelect.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(customer){
    this.setState({
      selectedItem: customer,
      initialLocation:{
        lat: customer.latitude,
        lng: customer.longitude
      }
    })
  }

  //The active tab must be set into the state
  //so that the Tabs component knows about the change and re-renders
  handleSelect(selectedTab) {
    this.setState({
      activeTab: selectedTab
    })
  }

  handleCategory(selectedCategory){
    this.setState({category: selectedCategory})
  }

  //Function which is called the first time the component loads
  componentDidMount() {

    navigator.geolocation.getCurrentPosition(
      position => {
        const { latitude, longitude } = position.coords;

        this.setState({
          currentLocation: { lat: latitude, lng: longitude },
          loading: false
        });
      },
      () => {
        this.setState({ loading: false });
      }
    );

    this.getCustomerData();
    
  }

  //Function to get the Customer Data from json
  getCustomerData() {
    axios.get('assets/samplejson/customerlist.json').then(response => {
      this.setState({customerList: response})
    })
  };

  // show infowindow when the marker is clicked
  onMarkerClick = (props, marker, e) =>{
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true,
    });
  }

  // close infowindow when the user clicks on the close button 
  onClose = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null,
        activePanel: null
      });
    }
  };

  // display markers
  displayMarkers= (poi, category) =>{
    return poi.map((poi,index) => {
      if(poi.category===category || category==="" || poi.second_category===category){
      return <Marker Key={index} id={index} position={{
        lat: poi.latitude,
        lng: poi.longitude,
      }}
      onClick={this.onMarkerClick}
      name={poi.name}
      icon={{url: require(`./icons/${poi.icon}.png`),
      scaledSize:  new this.props.google.maps.Size(25,25)}} />
    } 
  } )}

  togglePopup() {  
    this.setState({  
         showPopup: !this.state.showPopup,
         category:"stamp"  
  });  
  } 

  handlePopupClick(customer){
    this.setState({
      selectedItem: customer,
      initialLocation:{
        lat: customer.latitude,
        lng: customer.longitude
      },
      showPopup: !this.state.showPopup 
    })
  }

  render() {

    const{initialLocation, currentLocation} = this.state

    if (!this.state.customerList)
      return (<p>Loading data</p>)

    return (
      <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <h1 className="App-title">Welcome to Little India!</h1>
      </header>
    
      <div className="addmargin">

      <div className="floating-panel">
        <select id="type" onChange={(e)=> {this.setState({category: e.target.value})}}>
            <option value="">Please select category</option>
            <option value="top10">Top 10 Places to Visit</option>
            <option value="culture">Culture</option>
            <option value="food">Food and Drinks</option>
            <option value="shop">Shop and Experience</option>
            <option value="art">Arts</option>
        </select>   
      </div>
      <br line-height="5%"></br>

      <div id='map'>

      <Map
      className={'google-map'}
      google={this.props.google}
      style={style}
      center={new window.google.maps.LatLng(initialLocation.lat,initialLocation.lng)}
      initialCenter={{
        lat: 1.308480,
        lng: 103.853060
      }}
      zoom={16}
      onClick={()=>{this.mapClicked}}
      gestureHandling={'greedy'}
      >     

      {this.displayMarkers(stores,this.state.category)}

      <Polygon
      paths={littleindiaBoundary}
      strokeColor="#0000FF"
      strokeOpacity={0.8}
      strokeWeight={2}
      fillColor="#0000FF"
      fillOpacity={0.35}
      />

      <Marker position={currentLocation} onClick={this.onMarkerClick} name={"You are here!"}
              icon="http://maps.google.com/mapfiles/kml/pal4/icon39.png"/>

      <Marker position={initialLocation} onClick={this.onMarkerClick} name={this.state.selectedItem.name}/>

      <InfoWindow
        marker={this.state.activeMarker}
        visible={this.state.showingInfoWindow}
        onClose={this.onClose}
      >
        <div className="Info-box">
          <h1>{this.state.selectedPlace.name}</h1>  
          
          {/* <div>
            <img src={require(`./sampleimages/${this.state.selectedImage}.jpg`)} />
          </div> */}

        </div>
      </InfoWindow>

      </Map>
      </div>

      <div className="addmargin">
      <div className="row">
      {
      this.state.customerList.data.map((customer) => (
        <Tabs activeKey={this.state.activeTab} onSelect={this.handleSelect} className="centeralign"
        onClick={() => this.setState({selectedCustomer: customer.id, category: customer.category})}>
        <Tab eventKey={customer.id} title={customer.name}></Tab>
        </Tabs> 
      ))
      }
      </div>

      <div className="col-md-6">
        <CustomerDetails val={this.state.selectedCustomer} handler={this.handleClick} closeInfo={this.onClose}/>
      </div>

      <div class="stamp-collection">
      <button className="stamp_button"><img src={stamp} className="stamp" onClick={this.togglePopup.bind(this)}/></button>
      {/* <button onClick={this.togglePopup.bind(this)}> Click To Launch Popup</button>   */}
      </div>
      
      <div className="stamp-popup">
      {this.state.showPopup ?  
      <Popup  
        text='Collect all 10 stamps from the top must-visit places & exchange for a limited Little India edition bagde!'
        handler={this.handlePopupClick.bind(this)} 
        closeInfo={this.onClose} 
        closePopup={this.togglePopup.bind(this)}
      />
      : null  
      }  
      </div> 

      </div>
      </div> 
      </div>
      )}
}

// export default App;
export default GoogleApiWrapper({
  apiKey: ('AIzaSyDj4msBkNl1a8j6I0wUU37FWX7C-OexnxI')
})(App);


